<template>
  <BaseLayout title="我的订单">

    <van-pull-refresh v-model="state.refreshing" @refresh="onRefresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        <van-row class="block" align="center" v-for="(item,index) in state.list" :key="index" @click="$router.push('order/' + item.id)">
          <van-col span="24" class="dt"><van-tag color="#CCC">{{item.dt_create}}</van-tag></van-col>
          <van-col span="12" class="id"># {{item.id}}</van-col>
          <van-col span="12" class="status" :style="{color:item.color}">{{item.status}}</van-col>
          <van-col span="19" class="detail">
            <van-image class="img" v-for="(v,k) in item.detail" :key="k" :src="v.image" fit="contain"></van-image>
          </van-col>
          <van-col span="5" class="amount">
            <div>共 {{item.total}} 件</div>
            <div>￥ {{item.amount}}</div>
          </van-col>
        </van-row>
      </van-list>
    </van-pull-refresh>

  </BaseLayout>
</template>

<style scoped>
.list {
  min-height: 500px;
  padding: 5px;
  background: #F9F9F9;
  border-top: solid 1px #EEE;
}
.list .block {
  margin-top: 45px;
  padding: 0 10px 10px 10px;
  background: #FFF;
  border-radius: 10px;
  border: solid 1px #F6F6F6;
  font-size: 12px;
}

.list .dt {
  position: relative;
  top: -25px;
  text-align: center;
}
.list .id {
  font-size: 14px;
  font-weight: 600;
}
.list .status {
  text-align: right;
}
.list .detail {
  float: left;
  width: 100%;
  height: auto;
  overflow-x: scroll;
}
.list .img {
  width: 80px;
  height: 80px;
  margin: 10px 3px 0 0;
  border: solid 1px #F6F6F6;
  border-radius: 5px;
}
.list .amount {
  border-left: solid 1px #EEE;
  font-size: 12px;
  text-align: right;
}
</style>

<script>
import {reactive,ref} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  setup() {
    const state = reactive({
      refreshing: false,
      loading: false,
      finished: false,
      list: [],
      offset: 0,
      limit : 20,
    });
    return {state};
  },

  mounted(){
    this.onRefresh();
  },

  computed: {
  },

  methods: {
    onRefresh() {
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/mall/orderlist?offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push({...item, ...{
            status: ["未付款", "已付款", "已发货", "交易成功", "交易关闭"][item.status],
            color: ["#CCC", "#666", "#00C", "#0C0", "#C00"][item.status],
          }});
        }
      });
    },
  }
}
</script>
